@import '../../utils/mixins.scss';
@import '../../utils/colors.scss';

.FirstQuizPage {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  flex-direction: row;
  text-align: center;
  padding: 30px 0 0;

  background-image: radial-gradient(50% 50% at 80% 30%, $pink-gradient 0%, $black-gradient 100%);

  @include onDesktop {
    background-image: radial-gradient(50% 50% at 70% 30%, rgba(255, 9, 9, 0.11) 0%, rgba(255, 255, 255, 0.033) 100%);
    width: 100%;
    padding: 0;
  }

  &__info {
    display: none;
    background-image: url("../../images/quiz-ill.svg");

    @include onDesktop {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 40px 35px 35px;
      width: 40%;
      height: 100vh;
    }

    @include on1440 {
      padding: 60px 53px 53px;
    }

    @include on1920 {
      padding: 80px 70px 70px;
    }
  }

  &__content-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;

    @include onDesktop {
      width: 60%;
    }
  }

  &__main {
    padding: 12% 0 0;

    @include onDesktop {
      padding: 0;
    }
  }

  &__title {
    margin: 0 0 4%;

    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    transform: translateX(30%);
    opacity: 0;
    animation: moveLeft 0.3s linear 0s 1 alternate forwards;

    @media (max-width: 374px) {
      font-size: 16px;
    }

    &--mobile {
      @include onDesktop {
        display: none;
      }
    }

    &--desktop {
      display: none;

      @include onDesktop {
        display: block;
      }
    }

    &--red {
      border-bottom: 2px solid $main-red;
      transition: 0.3s all;

      &:hover {
        background-color: $main-red;
        border-radius: 3px;
        cursor: pointer;
      }
    }

    @include onDesktop {
      font-size: 24px;
      text-align: left;
    }

    @media (min-width: 1440px) {
      font-size: 36px;
    }

    @media (min-width: 1920px) {
      font-size: 48px;
    }
  }

  &__radiobuttons {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 24px 0;

    @include onDesktop {
      justify-content: left;
    }
  }

  &__radiobutton {
    opacity: 0;
    display: flex;
    justify-content: left;
    padding-left: 1%;
    display: flex;
    align-items: center;
    gap: 13px;

    width: 45%;

    @include onDesktop {
      justify-content: left;
      margin-left: 10%;
    }

    &:hover {
      cursor: pointer;
    }

    &:nth-child(1) {
      animation: moveLeft 0.3s linear 0.2s 1 alternate forwards;
    }
    &:nth-child(2) {
      animation: moveLeft 0.3s linear 0.4s 1 alternate forwards;
    }
    &:nth-child(3) {
      animation: moveLeft 0.3s linear 0.6s 1 alternate forwards;
    }
    &:nth-child(4) {
      animation: moveLeft 0.3s linear 0.8s 1 alternate forwards;
    }
  }

  &__radio {
    display: none;

    &:checked + 
    .FirstQuizPage__fake::before {
      opacity: 1;
    }
  }

  &__fake {
    display: inline-block;
    height: 47px;
    width: 47px;
    position: relative;

    &--red {
      border: 3px solid $main-red;
      border-radius: 50%;

      @include checkbox {
        background-color: $main-red;
      }
    }

    &--yellow {
      border: 3px solid $yellow-radiobutton;
      border-radius: 50%;

      @include checkbox {
        background-color: $yellow-radiobutton;
      }
    }

    &--green {
      border: 3px solid $green-radiobutton;
      border-radius: 50%;

      @include checkbox {
        background-color: $green-radiobutton;
      }
    }

    &--blue {
      border: 3px solid $blue-radiobutton;
      border-radius: 50%;

      @include checkbox {
        background-color: $blue-radiobutton;
      }
    }
  }

  &__arrow {
    display: none;
    position: absolute;
  
    @include onDesktop{
      left: 6%;
      display: block;
    }

    &--mobile {
      display: flex;
      justify-content: center;

      @include onDesktop {
        display: none;
      }
    }
  }

  &__text {
    font-family: 'Balsamiq Sans', cursive;
    font-size: 32px;
    color: #1a1a1c;

    @include on1440 {
      font-size: 43px;
    }

    @include on1920 {
      font-size: 64px;
    }
  }

  &__illustration {
    width: 155px;
    height: 333px;
    z-index: -1;
    position: absolute;
    bottom: 7%;
    right: 1%;

    opacity: 0;
    transform: translateY(100%);
    animation: moveUp 0.3s linear 1s 1 alternate forwards;

    @include onTablet {
      right: 50px;
    }

    @include onDesktop {
      z-index: 1;
      bottom: 7%;
      right: 35%;
    }

    @include on1440 {
      width: 206px;
      height: 444px;
    }

    @include on1920 {
      width: 309px;
      height: 665px;
    }
  }

  &__navigate {
    display: block;
    width: 40%;

    @include onDesktop {
      width: 60%;
    }
  }
}

@keyframes moveLeft {
  from {
    transform: translateX(30%);
    opacity: 0;
  }

  to {
    transform: translateX(0%);
    opacity: 1000;
  }
}

@keyframes moveUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }

  to {
    transform: translateY(0%);
    opacity: 100;
  }
}
