@import '../../utils/mixins.scss';

.modal {
  height: 100vh;
  width: 100vw;
  z-index: 4;
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(15px);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;

  &--active {
    opacity: 1;
    pointer-events: all;
  }
}

.modal__content {
  padding: 20px;
  border-radius: 12px;
  background-color: #fff;
  width: 100vw;
  transform: scale(0.5);
  transition: 0.4s all;

  @include onDesktop {
    width: 50vw;
  }

  &--active {
    transform: scale(1);
  }
}
